import React, { Component } from 'react'
import PubSub from 'pubsub-js';
import SubComponent from './SubComponent'

export default class PubComponent extends Component {
    componentDidMount() {
        // publish  publish_one This is publish
        PubSub.publish("publish_one", "This is data from publish")
    }
    sendData = () => {
        PubSub.publish("publish_one", "This is data from button click")
    }
    render() {
        return (
            <div>
                <h3>PubComponent works</h3>
                <input type="button" onClick={this.sendData} value="send"></input>
                <SubComponent />
            </div>
        )
    }
}
